import React, { useState, useEffect } from 'react'
import { VerticalTimeline, VerticalTimelineElement } from '../Shared/timeline';
// import Loading2 from '../../Shared/Loading2'
// import NumberFormat from 'react-number-format'
import config from '../../config'
import { MdShoppingCart, MdDateRange } from 'react-icons/md'
import { FaQrcode } from 'react-icons/fa'


const GiftItem = (props) => {
  const [data, setData] = useState(props.data);

  useEffect(() => {
    setData(props.data)
    console.log(props.data);

  }, [props.data])
  return (

    <React.Fragment>
      <VerticalTimelineElement
        className={data.gift ? "timeline-data-container has-gift" : "timeline-data-container"}
      // position={[1, 3, 4].includes(data.dataStat) ? "left" : "right"}
      >

        <div className={data.gift ? "timeline-data  pointer" : "timeline-data pointer"}>

          <div>
            <img src={data.item.image ? (config.uploadsUrl + data.item.image) : '/assets/images/no_image.png'} alt="" width="80px" />
            <span>{data.name}</span>
          </div>

          <div className="timeline-infos">

            <div className="row">
              <div className="col-sm-12 col-xs-12">
                <div className="timeline-item">
                  {/* <span className="key">نام محصول:</span> */}
                  <span className="value">
                    {data.item.name + ' (' + data.item.brand + ')'}
                  </span>
                </div>
              </div>
              {/* <div className="col-sm-6 col-xs-6">
                <div className="timeline-item">
                  <span className="key">نام برند:</span>
                  <span className="value">
                    {data.item.brand}
                  </span>
                </div>
              </div> */}
              {data.shop ?
                <div className="col-sm-12 col-xs-12">
                  <div className="timeline-item">

                    <span className="value">
                      {data.shop.name}<MdShoppingCart size="24px" color="#00bcd4" />
                    </span>
                  </div>
                </div>
                : ''}
              <div className="col-sm-12 col-xs-12">
                <div className="timeline-item" style={{border: 'none'}}>
                  {/* <span className="key">تاریخ اسکن:</span> */}
                  <span className="value">
                   {data.has_gift ? ' (' + data.gift.value + ')' : ''} {data.scan_date}<FaQrcode size="24px" color="#00bcd4" />
                    
                  </span>
                </div>
              </div>
              {/* {data.has_gift ?
                <div className="col-sm-12 col-xs-12">
                  <div className="timeline-item">
                    <span className="key">کد جایزه:</span>
                    <span className="value">
                      {data.uuid}
                    </span>
                  </div>
                </div>
                : ''} */}
            </div>

          </div>
        </div>

      </VerticalTimelineElement>

    </React.Fragment>

  )
}

export default GiftItem;
