import React, { Component, useState } from 'react'


const UserInfo = props => {
  const [data, setData] = useState(props.data)

  return (
    data &&
    <div>
      <div className="row">
        <div className="col-md-4">استان و شهرستان: {data.state + ' ' + data.city}</div>
        <div className="col-md-4">نام و نام خانوادگی: {data.firs_name + ' ' + data.last_name}</div>
        <div className="col-md-4">موبایل: {data.mobile}</div>
      </div>
      <div className="row">
        <div className="col-md-4">تاریخ تولد: {data.dob}</div>
        <div className="col-md-4">میزان اعتبار: {data.credit}</div>
      </div>
    </div>

  )
}
export default UserInfo;