import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import { PolarChart } from '../Charts/charts/PolarChart'
import { LineChart } from '../Charts/charts/LineChart'
import { BarChart } from '../Charts/charts/BarChart'
import { GenderPercentageChart } from '../Charts/charts/GenderPercentageChart'
import states from '../../../_constants/states'
import { FaUserCheck, FaQrcode } from 'react-icons/fa'
import { MdShoppingCart, MdCardMembership } from 'react-icons/md'
// import { IoIosBarcode } from 'react-icons/io'
import { AiOutlineLoading, AiOutlineQrcode } from 'react-icons/ai'
import config from '../../../config'
import { MdCheck } from 'react-icons/md'


import { dashboardService } from '../../../_services/dashboard.service'
import '../../../assets/iranmap/css/map.css'
import '../../../assets/iranmap/css/style.css'

class Tab1 extends Component {

  constructor(props, context) {
    super(props, context);
    // const travelBrandId = JSON.parse(localStorage.getItem('user')).travelBrand.travelBrandId
    this.state = {
      data: {},
      loading: true,
      stateId: undefined,
    }

  }

  componentDidMount() {

    this.getData()

  }

  getData() {
    this.setState({ loading: true })
    dashboardService.getStatisticsAsync(this.state.stateId)
      .then((res) => {
        console.log(res.data)
        this.setState({
          data: res.data,
          loading: false
        });
      })
      .catch((error) => {
        this.setState({ loading: false })
      })
  }

  handleStateChange = stateId => {

    this.setState({ stateId: this.state.stateId != stateId ? stateId : undefined }, () => {
      this.getData()
    })

  }

  render() {
    const { data, loading, stateId } = this.state;
    const { handleStateChange } = this;
    return (
      <>

        <div className="row">
          <div className="col-md-12" style={{ display: 'relative' }}>

            <div className="col-lg-3 col-md-3 col-sm-6 col-xs-6">
              <Link to={"/admin/shops/" + (stateId ? stateId : '')}>
                <div className="card card-stats">
                  <div className="card-header card-header-main card-header-icon">
                    <div className="card-icon">
                      <MdShoppingCart className="mdi" size="36px" className="material-icons" />
                    </div>
                    <p className="card-category">فروشگاه ها</p>
                    <h3 className="card-title">
                      تعداد کل:&nbsp;&nbsp;{loading ?
                        <AiOutlineLoading size="18px" className="mdi mdi-spin" />
                        :
                        data.shop_count
                      }
                    </h3>
                  </div>
                  {/* <div className="card-footer">
                    <div className="stats">
                      <FaRegCircle size="14px" className="material-icons" />
                      ثبت نام امروز:&nbsp;&nbsp;
                {loading ?
                        <AiOutlineLoading size="16px" className="mdi mdi-48px mdi-spin mdi-loading" />
                        :
                        data.shop_count
                      }
                    </div>
                  </div> */}
                </div>
              </Link>
            </div>

            <div className="col-lg-3 col-md-3 col-sm-6 col-xs-6">
              <Link to={"/admin/shops/" + (stateId ? stateId : '')}>
                <div className="card card-stats">
                  <div className="card-header card-header-danger card-header-icon">
                    <div className="card-icon">
                      <FaQrcode className="mdi" size="36px" className="material-icons" />
                    </div>
                    <p className="card-category">محصولات</p>
                    <h3 className="card-title">
                      تعداد کل:&nbsp;&nbsp;{loading ?
                        <AiOutlineLoading size="18px" className="mdi mdi-spin" />
                        :
                        data.shop_count
                      }
                    </h3>
                  </div>
                  {/* <div className="card-footer">
        <div className="stats">
          <FaRegCircle size="14px" className="material-icons" />
          ثبت نام امروز:&nbsp;&nbsp;
    {loading ?
            <AiOutlineLoading size="16px" className="mdi mdi-48px mdi-spin mdi-loading" />
            :
            data.shop_count
          }
        </div>
      </div> */}
                </div>
              </Link>
            </div>

            <div className="col-lg-3 col-md-3 col-sm-6 col-xs-6">
              <Link to="/admin/shops">
                <div className="card card-stats">
                  <div className="card-header card-header-success card-header-icon">
                    <div className="card-icon">
                      <FaUserCheck className="mdi" size="36px" className="material-icons" />
                    </div>
                    <p className="card-category">ثبت نام
                   <span style={{ whiteSpace: 'nowrap' }}> (امروز:&nbsp;&nbsp;
                {loading ?
                          <AiOutlineLoading size="16px" className="mdi mdi-48px mdi-spin mdi-loading" />
                          :
                          data.today_users
                        }
                      </span>
                      )
                    </p>
                    <h3 className="card-title">
                      تعداد کل:&nbsp;&nbsp;{loading ?
                        <AiOutlineLoading size="18px" className="mdi mdi-spin" />
                        :
                        data.user_count
                      }
                    </h3>
                  </div>
                  {/* <div className="card-footer">
                    <div className="stats">
                      <FaRegCircle size="14px" className="material-icons" />
                      ثبت نام امروز:&nbsp;&nbsp;
                {loading ?
                        <AiOutlineLoading size="16px" className="mdi mdi-48px mdi-spin mdi-loading" />
                        :
                        data.today_users
                      }
                    </div>
                  </div> */}
                </div>
              </Link>

            </div>

            <div className="col-lg-3 col-md-3 col-sm-6 col-xs-6">
              <Link to={"/admin/shops/" + (stateId ? stateId : '')}>
                <div className="card card-stats">
                  <div className="card-header card-header-warning card-header-icon">
                    <div className="card-icon">
                      <MdCardMembership className="mdi" size="36px" className="material-icons" />
                    </div>
                    <p className="card-category">جوایز
                    <span style={{ whiteSpace: 'nowrap' }}> (اسکن شده:&nbsp;&nbsp;
                {loading ?
                          <AiOutlineLoading size="16px" className="mdi mdi-48px mdi-spin mdi-loading" />
                          :
                          data.found_gifts
                        }
                      </span>
                    )
                    </p>
                    <h3 className="card-title">
                      تعداد کل:&nbsp;&nbsp;{loading ?
                        <AiOutlineLoading size="18px" className="mdi mdi-spin" />
                        :
                        data.gifts
                      }
                    </h3>
                  </div>
                  {/* <div className="card-footer">
                    <div className="stats">
                      <FaRegCircle size="14px" className="material-icons" />
                      ثبت نام امروز:&nbsp;&nbsp;
                {loading ?
                        <AiOutlineLoading size="16px" className="mdi mdi-48px mdi-spin mdi-loading" />
                        :
                        data.shop_count
                      }
                    </div>
                  </div> */}
                </div>
              </Link>
            </div>

          </div>
        </div>

        <div className="row display-flex">
          <div className="col-lg-8 col-md-8 col-sm-12 col-xs-12">
            <div class="svg-wrapper">

              <svg version="1.1" viewBox="0 0 841.89 595.28" enable-background="new 0 0 841.89 595.28" xmlSpace="preserve">

                <svg class="map-border">
                  <use xlinkHref="/mainMap.svg#map-border"></use>
                </svg>
                <svg class="map-sea">
                  <use xlinkHref="/mainMap.svg#map-sea"></use>
                </svg>
                <svg class="map-island">
                  <use xlinkHref="/mainMap.svg#map-island"></use>
                </svg>


                {states.map((item, i) => (
                  <a class="map-link" onClick={() => handleStateChange(item.id)}>
                    <svg class={"map-province " + item.name_en + (stateId === item.id ? " selected" : '')}>
                      <use xlinkHref={"/mainMap.svg#" + item.name_en}></use>
                    </svg>
                  </a >
                ))}


                <svg class="map-sea-names">
                  <use xlinkHref="/mainMap.svg#map-sea-names"></use>
                </svg >
                <svg class="map-province-names">
                  <use xlinkHref="/mainMap.svg#map-province-names"></use>
                </svg >

              </svg >


            </div >
          </div >
          <div className="col-lg-4 col-md-4 col-sm-12 col-xs-12 flex-align-center flex-center" style={{ display: 'relative' }}>
            <PolarChart data={data.data} />
          </div >

        </div >

        <div className="row display-flex" style={{ marginTop: 10 }}>
          <div className="col-md-8" style={{ display: 'relative' }}>
            <BarChart data={data.ages} />
          </div>
          <div className="col-md-4" style={{ display: 'relative' }}>
            <GenderPercentageChart data={{ male: data.male, female: data.female }} />
          </div>
        </div >

        <div className="row" style={{ marginTop: 10 }}>
          <div className="col-md-12" style={{ display: 'relative' }}>
            <LineChart data={data.chart} />
          </div>
        </div >

        <div className="row" style={{ marginTop: 30 }}>
          <div className="col-md-12" style={{ display: 'relative' }}>
            <fieldset className="fieldset">
              <legend>برندگان امروز</legend>
              <table className="table validator-table" style={{ marginBottom: 5 }}>
                <thead>
                  <tr>
                    <th className="text-center">ردیف</th>
                    <th className="text-center">تصویر محصول</th>
                    <th className="text-center">نام کالا</th>
                    <th className="text-center">برند</th>
                    <th className="text-center">نام فروشگاه</th>
                    {/* <th className="text-center">استان</th>
                    <th className="text-center">شهرستان</th> */}
                    <th className="text-center">نام</th>
                    <th className="text-center">نام خانوادگی</th>
                    <th className="text-center">موبایل</th>
                    <th className="text-center">تاریخ تولد</th>
                    <th className="text-center">جایزه</th>
                  </tr>
                </thead>
                <tbody>
                  {data && data.found_today && data.found_today.item_codes.map((item, index) => (
                    <tr key={"p-" + index}>
                      <td>{index + 1}</td>
                      <td>
                        <img style={{ height: 70, width: 80, display: 'inline' }} alt='' src={config.uploadsUrl + item.item.image} />
                      </td>
                      <td>
                        {item.item.name}
                      </td>
                      <td>
                        {item.item.brand}
                      </td>
                      <td>
                        {item.item.shop ? item.item.shop.name : ''}
                      </td>
                      {/* <td>{item.user.state}</td>
                      <td>{item.user.city}</td> */}
                      <td>{item.user.first_name}</td>
                      <td>{item.user.last_name}</td>
                      <td>{item.user.mobile}</td>
                      <td>{item.user.dob}</td>
                      <td>
                        {item.gift.value}
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </fieldset>
          </div>
        </div >

        <div className="row" style={{ marginTop: 30 }}>
          <div className="col-md-12" style={{ display: 'relative' }}>
            <fieldset className="fieldset">
              <legend>برندگان دیروز</legend>
              <table className="table validator-table" style={{ marginBottom: 5 }}>
                <thead>
                  <tr>
                    <th className="text-center">ردیف</th>
                    <th className="text-center">تصویر محصول</th>
                    <th className="text-center">نام کالا</th>
                    <th className="text-center">برند</th>
                    <th className="text-center">نام فروشگاه</th>
                    {/* <th className="text-center">استان</th>
                    <th className="text-center">شهرستان</th> */}
                    <th className="text-center">نام</th>
                    <th className="text-center">نام خانوادگی</th>
                    <th className="text-center">موبایل</th>
                    <th className="text-center">تاریخ تولد</th>
                    <th className="text-center">جایزه</th>
                  </tr>
                </thead>
                <tbody>
                  {data && data.found_yesterday && data.found_yesterday.item_codes.map((item, index) => (
                    <tr key={"p-" + index}>
                      <td>{index + 1}</td>
                      <td>
                        <img style={{ height: 70, width: 80, display: 'inline' }} alt='' src={config.uploadsUrl + item.item.image} />
                      </td>
                      <td>
                        {item.item.name}
                      </td>
                      <td>
                        {item.item.brand}
                      </td>
                      <td>
                        {item.item.shop ? item.item.shop.name : ''}
                      </td>
                      {/* <td>{item.user.state}</td>
                      <td>{item.user.city}</td> */}
                      <td>{item.user.first_name}</td>
                      <td>{item.user.last_name}</td>
                      <td>{item.user.mobile}</td>
                      <td>{item.user.dob}</td>
                      <td>
                        {item.gift.value}
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </fieldset>
          </div>
        </div >

      </>
    );
  }
}

export default Tab1;